.block {
  width: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
}

.img {
  border-radius: 50%;
}

.block:hover {
  box-shadow: 0 10px 30px rgb(0 0 0 / 90%);
  transition: 0.5s ease-in-out;
}
